* {
  font-family: "Montserrat", sans-serif;
}
body {
  color: white;
  background-color: black;
}
.logo:hover {
  background-color: black;
}
.navbar {
  background-color: black;
  .about {
    background-color: #ff4900;
  }
  li {
    list-style-type: none;
    a {
      color: white;
    }
  }
  .button {
    button {
      background-color: white;
    }
    button:hover {
      background-color: #ff4900;
    }
  }
}
.body {
  .text {
    .hire {
      background-color: #ff4900;
      color: white;
    }
    .hire:hover {
      color: white;
      background-color: black;
      border: solid white;
    }
    .explore {
      color: white;
      background-color: black;
      border: solid white;
    }
    .explore:hover {
      background-color: #ff4900;
      color: white;
    }
  }
}
.main {
  #service_icon {
    .service {
      background-color: grey;
      min-height: 40%;
      text-align: center;
    }
    .service:hover {
      background-color: #ff4900;
    }
  }
  button {
    color: white;
    background-color: #ff4900;
  }
  button:hover {
    color: #ff4900;
    background-color: white;
  }
}
.subscribe {
  text-align: center;
  font-size: 20px;
  font-family: "Courier New", Courier, monospace;
  width: 50%;
  height: 30%;
  background-color: #041c32;
  span {
    color: #ff4900;
  }
  input {
    width: 80%;
  }
  button {
    color: white;
    background-color: #ff4900;
  }
  button:hover {
    color: #ff4900;
    background-color: white;
  }
}
#contact {
  span {
    color: #ff4900;
  }
}
.footer {
  width: 100%;
}
.icon {
  svg {
    margin-left: 15px;
  }
}
hr {
  background-color: #ff4900;
}
